import React from 'react';
import Bulma from '@vizuaalog/bulmajs';
const NavBar = (props)=>{
    return(
<nav className="navbar is-primary "style={{width:"vw"}}>
  <div className="navbar-brand">
    <a className="navbar-item" href="#">
        <img src="data:image/svg+xml;base64," />
        {'\u00A0'}
        Portafolio
    </a>
    <div className="navbar-burger" data-target="navbarExampleTransparentExample">
      <span></span>
      <span></span>
      <span></span>
    </div>
  </div>

  <div id="navbarExampleTransparentExample" className="navbar-menu">
    <div className="navbar-start">
      <a className="navbar-item" href="#">
        Home
      </a>
      <div className="navbar-item has-dropdown is-hoverable">
        <a className="navbar-link" href="#">
          Docs
        </a>
        <div className="navbar-dropdown is-boxed">
          <a className="navbar-item" href="#">
            Overview
          </a>
          <a className="navbar-item" href="#">
            Modifiers
          </a>
          <a className="navbar-item" href="#">
            Columns
          </a>
          <a className="navbar-item" href="#">
            Layout
          </a>
          <a className="navbar-item" href="#">
            Form
          </a>
          <hr className="navbar-divider"/>
          <a className="navbar-item" href="#">
            Elements
          </a>
          <a className="navbar-item is-active" href="https://bulma.io/documentation/components/breadcrumb/">
            Components
          </a>
        </div>
      </div>
    </div>

    <div className="navbar-end">
      <div className="navbar-item">
        <div className="field is-grouped">
          <p className="control">
            <button className="bd-tw-button button" title="proximamente">
              <span className="icon">
                <i className="fab fa-twitter"></i>
              </span>
              <span>
                MS Desing Company
              </span>
            </button>
          </p>
          <p className="control" style={{width:"40%"}}>
            <a style={{width:"100%"}} className="button button is-warning is-focused" href="https://github.com/jgthms/bulma/releases/download/0.9.2/bulma-0.9.2.zip">
              <span className="icon">
                <i className="fas fa-download"></i>
              </span>
              <span>Descargar C.V</span>
            </a>
          </p>
        </div>
      </div>
    </div>
  </div>
</nav>   
    )
}
export default NavBar;